<template>
    <div class="Studycontent">
        <div class="content">
            <template v-for="(items) in Message.result">
                <li @click="goStudyvideo(item)" v-for="(item, index) in items" :key="index">
                    <div class="group">
                        <div class="img-group">
                            <img :src="item.videoThumbnail">
                        </div>
                        <div class="text-group">
                            <h2>{{item.title}}</h2>
                            <p>
                                <span v-if="parseInt(item.videoDuration/60)<10">0</span>
                                <span>{{parseInt(item.videoDuration/60)}}:</span>
                                <span v-if="parseInt(item.videoDuration%60)<10">0</span>
                                <span>{{parseInt(item.videoDuration%60)}}</span>
                            </p>
                        </div>
                        <div class="icon-group">
                            <i class="fa fa-angle-right"></i>
                        </div>
                    </div>
                </li>
            </template>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Studycontent',
        props: ['Message'],
        methods: {
            goStudyvideo(item) {
                // console.log(item.title)
                this.$router.push({
                    name: 'studyvideocontent',
                    params: {
                        title: item.title,
                        videoThumbnail: item.videoThumbnail,
                        videosrc: item.src,
                        videotext: item.text
                    }
                })
            }
        },
     
    }
</script>

<style lang="less">
    .Studycontent {
        .content {
            padding: 1rem;

            li {
                display: flex;
                flex-wrap: wrap;
                padding: 1rem 0;
                border-bottom: 0.1rem solid lightgray;

                &:nth-last-child(1) {
                    border: none;
                }

                .group {
                    display: flex;
                    flex-basis: 100%;

                    .img-group {
                        flex-basis: 150%;

                        img {
                            max-width: 100%;
                        }
                    }

                    .text-group {
                        flex-basis: 150%;
                        padding: 1rem;
                        display: flex;
                        flex-wrap: wrap;
                        align-content: space-between;

                        h2 {
                            font-size: 1.5rem;
                            flex-basis: 100%;
                        }

                        p {
                            flex-basis: 100%;

                            span {
                                font-size: 1.3rem;
                            }
                        }
                    }

                    .icon-group {
                        text-align: right;
                        flex-basis: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: flex-end;

                        i {
                            font-size: 2rem;
                            color: lightgrey;
                        }
                    }
                }
            }
        }
    }
</style>